.scroll-to-top{position:fixed;bottom:20px;right:20px;width:40px;height:40px;border-radius:50%;background-color:#333;color:#fff;border:none;outline:none;cursor:pointer;opacity:0;transition:opacity,.5s}.scroll-to-top.visible{opacity:1;transition:opacity,.5s}.scroll-to-top:hover{background-color:#555}.scroll-to-top:before{content:"▲";font-size:20px;line-height:40px;text-align:center}